import React from 'react'
import { Droppable } from 'react-beautiful-dnd'
import Task from '../Task/task.jsx'
import Typography from '@material-ui/core/Typography'
import ButtonAdd from '../ButtonAdd/button-add.jsx'

const List = ({ id, name, tasks }) => {
  return (
    <div style={{ margin: '0px 5px' }}>
      <Droppable droppableId={id}>
        {(provided, snapshot) => {
          return (
            <div
              {...provided.droppableProps}
              ref={provided.innerRef}
              style={{
                minWidth: 250,
                maxWidth: 250,
                maxHeight: '88vh',
                padding: '4px',
                borderRadius: '4px',
                overflowY: 'auto',
                background: snapshot.isDraggingOver ? '#0f346050' : '#f4f4f490'
              }}
            >
              <Typography
                variant="h5"
                component="h2"
                className="txt-center nooverflow"
              >
                {name}
              </Typography>
              {tasks.map((task, index) => (
                <Task key={index} data={task} index={index} />
              ))}
              {provided.placeholder}
              <ButtonAdd />
            </div>
          )
        }}
      </Droppable>
    </div>
  )
}

export default List
