import React from 'react'
import RouterView from './router/router-view.jsx'
import AppLoader from './components/AppLoader/app-loader.jsx'
import { connect } from 'react-redux'
import 'animate.css'
import { ThemeProvider } from '@material-ui/core/styles'
import { createMuiTheme } from '@material-ui/core/styles'

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#16213e'
    },
    secondary: {
      main: '#e94560'
    }
  }
})

function App({ auth, appLoader }) {
  console.log('Authenticated', auth?.status)
  return (
    <ThemeProvider theme={theme}>
      <div className="App">
        <RouterView itIsAutorized={auth.status} />
        <AppLoader active={appLoader.active} />
      </div>
    </ThemeProvider>
  )
}

const getState = ({ authorizedReducer, appLoader }) => ({
  auth: authorizedReducer,
  appLoader: appLoader
})

export default connect(getState)(App)
