import React from 'react'
import { Modal, Button, Typography, TextField } from '@material-ui/core'
import { changeTaskModalState, setNewTask } from '../../store/actions.js'
import { connect } from 'react-redux'
import { useParams } from 'react-router-dom'
import {
  animateIn,
  animateOut,
  invalidValue,
  listsToArray
} from '../../utils/utils.js'

const data = { name: '', description: '', listId: '' }

const changeData = (e, type) => {
  data[type] = e.target.value
}

const cleanData = () => {
  data.name = ''
  data.description = ''
  data.listId = ''
}

const TaskModal = ({ active, lists, taskModal, setNewTask }) => {
  let arrayList = listsToArray(lists)
  const projectId = useParams('id').id
  arrayList = arrayList.filter(list => list.projectId === projectId)
  const [animation, setAnimation] = React.useState(animateIn)

  const close = () => {
    setAnimation(animateOut)
    setTimeout(() => {
      taskModal(false)
      setAnimation(animateIn)
    }, 500)
  }

  const createTask = () => {
    if (!invalidValue(data?.name) && !invalidValue(data?.listId)) {
      setNewTask(data)
    }

    cleanData()
    close()
  }

  return (
    <Modal className={animation} open={active} onClose={close}>
      <div className="modal-content">
        <Typography variant="h4" color="primary">
          New Task
        </Typography>
        <form>
          <div className="mini-container">
            <TextField
              id="standard-basic"
              label="Task Name"
              onChange={e => changeData(e, 'name')}
            />
          </div>
          <div className="mini-container">
            <TextField
              id="standard-basic"
              label="Task Description"
              multiline
              onChange={e => changeData(e, 'description')}
            />
          </div>
          <div className="mini-container">
            <select onChange={e => changeData(e, 'listId')}>
              <option value={''}>Select a List</option>
              {arrayList.map(item => (
                <option value={item.id} key={item.id}>
                  {item.name}
                </option>
              ))}
            </select>
          </div>
          <div className="btn-container">
            <Button color="secondary" variant="contained" onClick={close}>
              Cancel
            </Button>
            <Button color="primary" variant="contained" onClick={createTask}>
              Create
            </Button>
          </div>
        </form>
      </div>
    </Modal>
  )
}

const getState = ({ lists }) => ({
  lists: lists
})

const setState = dispatch => ({
  taskModal: newState => dispatch(changeTaskModalState(newState)),
  setNewTask: newTaskData => dispatch(setNewTask(newTaskData))
})

export default connect(getState, setState)(TaskModal)
