import React from 'react'
import AppHeader from '../../components/AppHeader/app-header.jsx'
import ProjectCard from '../../components/ProjectCard/project-card'
import ProjectModal from '../../components/ProjectModal/project-modal.jsx'
import DeleteModal from '../../components/DeleteModal/delete-modal.jsx'
import Alert from '@material-ui/lab/Alert'
import { connect } from 'react-redux'
import { setProjectBackground } from '../../utils/utils.js'

const Projects = ({ projs, pmodal, dmodal }) => {
  setProjectBackground()

  return (
    <div>
      <AppHeader />
      <div className="projects-list">
        {projs.length > 0 ? (
          projs.map(item => (
            <ProjectCard
              name={item.name}
              description={item.description}
              image={item.image}
              id={item.id}
              key={item.id}
            />
          ))
        ) : (
          <div className="center-container">
            <Alert severity="info">There are no projects yet</Alert>
          </div>
        )}
      </div>
      <ProjectModal active={pmodal.active} />
      <DeleteModal active={dmodal.active} info={dmodal.info} type="Project" />
    </div>
  )
}

const getState = ({ projects, projectModal, deleteModal }) => ({
  projs: projects,
  pmodal: projectModal,
  dmodal: deleteModal
})

export default connect(getState)(Projects)
