import React from 'react'
import { useParams } from 'react-router-dom'
import { Modal, Button, Typography, TextField } from '@material-ui/core'
import { changeListModalState, setNewList } from '../../store/actions.js'
import { connect } from 'react-redux'
import { animateIn, animateOut, invalidValue } from '../../utils/utils.js'

const data = { name: '', projectId: '' }

const changeData = (e, type) => {
  data[type] = e.target.value
}

const cleanData = () => {
  data.name = ''
  data.projectId = ''
}

const ListModal = ({ active, listModal, setNewList }) => {
  const [ animation, setAnimation ] = React.useState(animateIn)

  data.projectId = useParams('id').id

  const close = () => {
    setAnimation(animateOut)
    setTimeout(() => {
      listModal(false)
      setAnimation(animateIn)
    }, 500)
  }

  const createList = () => {
    if (!invalidValue(data?.name)) {
      setNewList(data)
    }
    cleanData()
    close()
  }

  return (
    <Modal className={animation} open={active} onClose={close}>
      <div className="modal-content">
        <Typography variant="h4" color="primary">
          New List
        </Typography>
        <form>
          <div className="mini-container">
            <TextField
              id="standard-basic"
              label="List Name"
              onChange={e => changeData(e, 'name')}
            />
          </div>
          <div className="btn-container">
            <Button color="secondary" variant="contained" onClick={close}>
              Cancel
            </Button>
            <Button color="primary" variant="contained" onClick={createList}>
              Create
            </Button>
          </div>
        </form>
      </div>
    </Modal>
  )
}

const setState = dispatch => ({
  listModal: newState => dispatch(changeListModalState(newState)),
  setNewList: newListData => dispatch(setNewList(newListData))
})

export default connect(null, setState)(ListModal)
