import React from 'react'
import { Route } from 'react-router-dom'

const createRoutes = routesObj => {
  return routesObj.map(item => {
    const finalRoute = item.withParams ? (
      <Route exact path={item.path} key={item.name}>
        {item.view}
      </Route>
    ) : (
      <Route path={item.path} children={item.view} key={item.name} />
    )

    return finalRoute
  })
}

const RouterCore = (authorized, authorizedRoutes, unauthorizedRoutes) =>
  authorized !== false
    ? createRoutes(authorizedRoutes)
    : createRoutes(unauthorizedRoutes)

export default RouterCore
