import { createStore, combineReducers } from 'redux'
import {
  firebaseReducer,
  firestoreReducer,
  authorizedReducer,
  projects,
  lists,
  projectModal,
  listModal,
  taskModal,
  deleteModal,
  currentData,
  appLoader
} from './reducers.js'

const reducers = combineReducers({
  firebaseReducer,
  firestoreReducer,
  authorizedReducer,
  projects,
  lists,
  projectModal,
  listModal,
  taskModal,
  deleteModal,
  currentData,
  appLoader
})

const Store = () => createStore(reducers)

export default Store
