import React from 'react'
import { connect } from 'react-redux'
import { Modal, Button, Typography, TextField } from '@material-ui/core'
import AppLoader from '../AppLoader/app-loader.jsx'
import { changeProjectModalState, setNewProject } from '../../store/actions.js'
import {
  animateIn,
  animateOut,
  invalidValue,
  createId,
  getImageBackground
} from '../../utils/utils.js'

const data = { name: '', id: '', image: '' }

const ProjectModal = ({ active, projectModal, setNewProject }) => {
  const [animation, setAnimation] = React.useState(animateIn)
  const [loaderStatus, chLoad] = React.useState(false)

  const close = () => {
    setAnimation(animateOut)
    setTimeout(() => {
      projectModal(false)
      setAnimation(animateIn)
    }, 500)
  }

  const changeData = (e, type) => {
    data[type] = e.target.value
  }

  const cleanData = () => {
    data.id = ''
    data.name = ''
    data.image = ''
  }

  const createProject = async e => {
    if (!invalidValue(e)) {
      e.preventDefault()
    }
    chLoad(true)
    data.id = createId()
    if (!invalidValue(data?.name)) {
      try {
        let res = await getImageBackground()
        data.image = res
        console.log(data)
        setNewProject(data)
      } catch (e) {
        console.warn(e)
      }
    }

    chLoad(false)
    cleanData()
    close()
  }

  return (
    <Modal className={animation} open={active} onClose={close}>
      <div className="modal-content">
        <Typography variant="h4" color="primary">
          New Project
        </Typography>
        <form action="#" onSubmit={e => createProject(e)}>
          <div className="mini-container">
            <TextField
              id="standard-basic"
              label="Project Name"
              onChange={e => changeData(e, 'name')}
            />
          </div>
          <div className="btn-container">
            <Button color="secondary" variant="contained" onClick={close}>
              Cancel
            </Button>
            <Button
              color="primary"
              variant="contained"
              onClick={e => createProject(e)}
            >
              Create
            </Button>
          </div>
        </form>
        <AppLoader active={loaderStatus} />
      </div>
    </Modal>
  )
}

const setState = dispatch => ({
  projectModal: newState => dispatch(changeProjectModalState(newState)),
  setNewProject: newProjectData => dispatch(setNewProject(newProjectData))
})

export default connect(null, setState)(ProjectModal)
