import React from 'react'
import { connect } from 'react-redux'
import { Button } from '@material-ui/core'
import { invalidValue } from '../../utils/utils.js'
import { withRouter } from 'react-router-dom'
import {
  MeetingRoom,
  AssignmentTurnedIn,
  List,
  Home,
  CreateNewFolder
} from '@material-ui/icons/'
import {
  changeAuthStatus,
  changeProjectModalState,
  changeListModalState,
  changeTaskModalState
} from '../../store/actions.js'

const AppHeader = props => {
  let {
    type,
    auth,
    changeAuth,
    projectModal,
    listModal,
    taskModal,
    history
  } = props

  type = invalidValue(type) ? 'project' : type 

  const inProjectPath = window.location.pathname.includes('/project/')

  const newProjectModal = () => {
    projectModal(true)
  }

  const newListtModal = () => {
    listModal()
  }

  const newTasktModal = () => {
    taskModal()
  }

  const goToLink = () => {
    history.push('/projects')
  }

  return (
    <div className="app-header">
      <div className="main-text">TooDo</div>
      <div className="header-btn-container">
        {type === 'project' ? (
          <Button
            color="inherit"
            size="small"
            variant="text"
            onClick={newProjectModal}
          >
            <div className="m-text">Project</div>
            <CreateNewFolder />
          </Button>
        ) : (
          <></>
        )}

        {type === 'lists' ? (
          <Button
            className=""
            color="inherit"
            size="small"
            variant="text"
            onClick={newListtModal}
          >
            <div className="m-text">List</div>
            <List />
          </Button>
        ) : (
          <></>
        )}

        {type === 'lists' ? (
          <Button
            className=""
            color="inherit"
            size="small"
            variant="text"
            onClick={newTasktModal}
          >
            <div className="m-text">Task</div>
            <AssignmentTurnedIn />
          </Button>
        ) : (
          <></>
        )}

        {inProjectPath ? (
          <Button
            className=""
            color="inherit"
            size="small"
            variant="text"
            onClick={goToLink}
          >
            <div className="m-text">Home</div>
            <Home />
          </Button>
        ) : (
          <></>
        )}

        {auth.status ? (
          <Button
            color="inherit"
            size="small"
            variant="text"
            onClick={changeAuth}
          >
            <div className="m-text">Logout</div>
            <MeetingRoom />
          </Button>
        ) : (
          <></>
        )}
      </div>
    </div>
  )
}

const getState = ({ authorizedReducer }) => ({ auth: authorizedReducer })

const setState = dispatch => ({
  changeAuth: () => dispatch(changeAuthStatus(false)),
  projectModal: () => dispatch(changeProjectModalState(true)),
  listModal: () => dispatch(changeListModalState(true)),
  taskModal: () => dispatch(changeTaskModalState(true))
})

export default connect(getState, setState)(withRouter(AppHeader))
