export const AUTHORIZED = 'AUTHORIZED'
export const NEW_PROJECT = 'NEW_PROJECT'
export const DELETE_PROJECT = 'DELETE_PROJECT'
export const NEW_LIST = 'NEW_LIST'
export const NEW_TASK = 'NEW_TASK'
export const UPDATE_TASK = 'UPDATE_TASK'
export const SET_CURRENT_DATA = 'SET_CURRENT_DATA'
export const CHANGE_P_MODAL = 'CHANGE_P_MODAL'
export const CHANGE_L_MODAL = 'CHANGE_L_MODAL'
export const CHANGE_T_MODAL = 'CHANGE_T_MODAL'
export const CHANGE_D_MODAL = 'CHANGE_D_MODAL'
export const CHANGE_LOADER = 'CHANGE_LOADER'

export const changeAuthStatus = authStatus => ({
  type: AUTHORIZED,
  payload: authStatus
})

export const setNewProject = newProject => ({
  type: NEW_PROJECT,
  payload: newProject
})

export const deleteProject = projectId => ({
  type: DELETE_PROJECT,
  payload: projectId
})

export const setNewList = newList => ({
  type: NEW_LIST,
  payload: newList
})

export const updateTask = (dest, source, taskId) => ({
  type: UPDATE_TASK,
  payload: { dest, source, taskId }
})

export const setNewTask = newTask => ({
  type: NEW_TASK,
  payload: newTask
})

export const setCurrentData = data => ({
  type: SET_CURRENT_DATA,
  payload: data
})

export const changeProjectModalState = modalStatus => ({
  type: CHANGE_P_MODAL,
  payload: modalStatus
})

export const changeListModalState = modalStatus => ({
  type: CHANGE_L_MODAL,
  payload: modalStatus
})

export const changeTaskModalState = modalStatus => ({
  type: CHANGE_T_MODAL,
  payload: modalStatus
})

export const changeDeleteModalState = modalStatus => ({
  type: CHANGE_D_MODAL,
  payload: modalStatus
})

export const changeLoaderState = loaderStatus => ({
  type: CHANGE_LOADER,
  payload: loaderStatus
})
