import uuid from 'uuid/v4'
import axios from 'axios'
import defaultImage from '../assets/images/ToodoBg.png'
import defaultProjectImage from '../assets/images/ToodoBgBlack.png'

export const animateIn =
  'modal animate__animated animate__faster animate__fadeIn'

export const animateOut =
  'modal animate__animated animate__faster animate__fadeOut'

export const listsToArray = lists => {
  let arrayList = Object.entries(lists)

  arrayList = arrayList.map(item => ({
    id: item[0],
    ...item[1]
  }))

  return arrayList
}

export const createId = () => {
  return uuid()
}

export const getImageBackground = async () => {
  const url = `https://source.unsplash.com/${window.screen.availWidth}x${window.screen.availHeight}/?nature,city,food`
  let image

  try {
    let data = await axios.get(url)
    image = data.request.responseURL
  } catch (e) {
    console.warn('Problem with Unsplash API')
    image = defaultProjectImage
  }

  return image
}

export const invalid = [undefined, null, '']

export const invalidValue = element => {
  return invalid.includes(element)
}

export const setProjectBackground = image => {
  image = invalidValue(image) ? defaultImage : image
  const body = document.querySelector('body')
  body.style = `background-image: url('${image}')`
}

export const validRoute = itIsAutorized => {
  const w = window
  if (!invalidValue(itIsAutorized) && w.location.pathname !== '/') {
    w.location = '/'
  }
}

export default {
  animateIn,
  animateOut,
  createId,
  invalid,
  invalidValue,
  validRoute,
  listsToArray
}
