import React from 'react'
import { useParams } from 'react-router-dom'
import { DragDropContext } from 'react-beautiful-dnd'
import { connect } from 'react-redux'
import { updateTask } from '../../store/actions.js'
import AppHeader from '../../components/AppHeader/app-header.jsx'
import List from '../../components/List/list.jsx'
import ListModal from '../../components/ListModal/list-modal.jsx'
import TaskModal from '../../components/TaskModal/task-modal.jsx'
import { listsToArray, setProjectBackground } from '../../utils/utils.js'

const Project = ({
  lists,
  listModalState,
  taskModalState,
  updateTask,
  projects
}) => {
  const projectId = useParams('id').id
  const currentProject = projects.find(item => item.id === projectId)
  setProjectBackground(currentProject.image)
  console.log(currentProject)
  let arrayList = listsToArray(lists)

  arrayList = arrayList.filter(list => list.projectId === projectId)

  const onDragEnd = result => {
    if (result.destination !== null) {
      const { draggableId, destination, source } = result
      updateTask(destination, source, draggableId)
    }
  }

  return (
    <div>
      <AppHeader type="lists" />
      <DragDropContext onDragEnd={result => onDragEnd(result)}>
        <div
          style={{
            minWidth: 200,
            display: 'flex',
            flexFlow: 'row nowrap',
            margin: '0px 10px 0px 10px',
            padding: '10px 0px 10px 0px',
            overflowY: 'auto'
          }}
        >
          {arrayList.map((list, index) => (
            <List
              key={list.id}
              id={list.id}
              name={list.name}
              tasks={list.tasks}
            />
          ))}
        </div>
      </DragDropContext>
      <ListModal active={listModalState.active} />
      <TaskModal active={taskModalState.active} />
    </div>
  )
}

const getState = ({ lists, listModal, taskModal, currentData, projects }) => ({
  projects: projects,
  lists: lists,
  listModalState: listModal,
  taskModalState: taskModal
})

const setState = dispatch => ({
  updateTask: (destination, source, taskId) =>
    dispatch(updateTask(destination, source, taskId))
})

export default connect(getState, setState)(Project)
