import firebase from '../services/Firebase/firebase.js'
import firestore from '../services/Firebase/firestore.js'
import uuid from 'uuid/v4'
import {
  AUTHORIZED,
  NEW_PROJECT,
  DELETE_PROJECT,
  NEW_LIST,
  NEW_TASK,
  UPDATE_TASK,
  SET_CURRENT_DATA,
  CHANGE_P_MODAL,
  CHANGE_L_MODAL,
  CHANGE_T_MODAL,
  CHANGE_D_MODAL,
  CHANGE_LOADER
} from './actions.js'

// FIREBASE
export const firebaseReducer = (state = firebase, { type, payload }) => {
  return state
}

// FIRESTORE
export const firestoreReducer = (state = firestore, { type, payload }) => {
  return state
}

// --- AUTHORIZATION ---
const authState = { status: true }
export const authorizedReducer = (state = authState, { type, payload }) => {
  switch (type) {
    case AUTHORIZED: {
      state.status = payload
      return { ...state }
    }

    default:
      return state
  }
}

// --- PROJECTS ---
const projectsState = []
export const projects = (state = projectsState, { type, payload }) => {
  switch (type) {
    case NEW_PROJECT: {
      state.unshift({
        id: payload.id,
        name: payload.name,
        description: payload.description,
        image: payload.image
      })
      return [...state]
    }

    case DELETE_PROJECT: {
      state = state.filter(item => item.id !== payload)
      return [...state]
    }

    default:
      return state
  }
}

// LIST
const listsState = {}
export const lists = (state = listsState, { type, payload }) => {
  switch (type) {
    case NEW_LIST: {
      state = {
        ...state,
        [uuid()]: {
          name: payload.name,
          projectId: payload.projectId,
          tasks: []
        }
      }
      return { ...state }
    }

    case NEW_TASK: {
      state[payload.listId].tasks.push({
        id: uuid(),
        title: payload.name,
        content: payload.description
      })
      return { ...state }
    }

    case UPDATE_TASK: {
      // ATTENTION: The 'payload' object also provides the 'taskId' element
      const { source, dest } = payload
      const [removed] = state[source.droppableId].tasks.splice(source.index, 1)
      state[dest.droppableId].tasks.splice(dest.index, 0, removed)
      return { ...state }
    }

    default:
      return state
  }
}

// Current Project And Lists Data
const currentDataState = {}
export const currentData = (state = currentDataState, { type, payload }) => {
  switch (type) {
    case SET_CURRENT_DATA: {
      state = { ...payload }
      return { ...state }
    }

    default:
      return state
  }
}

