import React from 'react'
import RouterCore from './router-core.jsx'
import { BrowserRouter as Router, Switch } from 'react-router-dom'
import Project from '../views/Project/project.jsx'
import Projects from '../views/Projects/projects.jsx'
import Login from '../views/Login/login.jsx'
import { validRoute } from '../utils/utils.js'

const authorizedRoutes = [
  {
    name: 'Project',
    path: '/project/:id',
    view: <Project />,
    withParams: true
  },
  {
    name: 'Projects',
    path: '/',
    view: <Projects />,
    withParams: false
  }
]

const unauthorizedRoutes = [
  {
    name: 'Login',
    path: '/',
    view: <Login />
  }
]

const RouterView = ({ itIsAutorized }) => {
  validRoute(itIsAutorized)

  return (
    <Router>
      <Switch>
        {RouterCore(itIsAutorized, authorizedRoutes, unauthorizedRoutes)}
      </Switch>
    </Router>
  )
}

export default RouterView
