import React from 'react'
import { Modal, Button, Typography } from '@material-ui/core'
import { changeDeleteModalState, deleteProject } from '../../store/actions.js'
import { connect } from 'react-redux'
import { animateIn, animateOut } from '../../utils/utils.js'

const DeleteModal = ({
  active,
  type,
  info,
  projects,
  deleteModal,
  deleteProject
}) => {
  const [ animation, setAnimation ] = React.useState(animateIn)

  const close = () => {
    setAnimation(animateOut)
    setTimeout(() => {
      deleteModal({ active: false, info: null })
      setAnimation(animateIn)
    }, 500)
  }

  const deleteAction = () => {
    deleteProject(info)
    close()
  }

  const element = projects.find(item => item.id === info)

  return (
    <Modal className={animation} open={active} onClose={close}>
      <div className="modal-content">
        <Typography variant="h4" color="primary">
          Delete {type}
        </Typography>
        <form>
          <div className="mini-container">
            <Typography variant="h6">
              Sure you want to delete this project?
            </Typography>
          </div>
          <div className="mini-container">
            <Typography variant="h5">{element?.name}</Typography>
            <Typography variant="body1">{element?.description}</Typography>
          </div>
          <div className="btn-container">
            <Button color="primary" variant="contained" onClick={close}>
              No
            </Button>
            <Button
              color="secondary"
              variant="contained"
              onClick={deleteAction}
            >
              Yes
            </Button>
          </div>
        </form>
      </div>
    </Modal>
  )
}

const getState = ({ projects }) => ({
  projects: projects
})

const setState = dispatch => ({
  deleteModal: newState => dispatch(changeDeleteModalState(newState)),
  deleteProject: projectId => dispatch(deleteProject(projectId))
})

export default connect(getState, setState)(DeleteModal)
