import React from 'react'
import { Link } from 'react-router-dom'
import { Card, CardContent, Typography } from '@material-ui/core'

const ProjectCard = ({ name, description, image, id }) => {
  const shadow = `
    0px 1px 2px black,
    0px -1px 2px black,
    -1px 0px 2px black,
    1px 0px 2px black,
    1px 1px 2px black,
    -1px -1px 2px black
  `

  const linkPath = `/project/${id}`

  return (
    <Link to={linkPath}>
      <Card
        style={{
          minWidth: 168,
          maxWidth: 168,
          minHeight: 96,
          maxHeight: 96,
          margin: '5px',
          backgroundImage: `url(${image})`,
          backgroundPosition: 'center'
        }}
        variant="outlined"
      >
        <CardContent>
          <Typography
            variant="h5"
            component="h2"
            style={{
              fontWeight: '500',
              color: 'white',
              textShadow: shadow
            }}
          >
            {name}
          </Typography>
        </CardContent>
      </Card>
    </Link>
  )
}

export default ProjectCard
